import axios from 'axios';
//localstorage
import storejs from 'storejs';
// element Loading 
import { Loading } from 'element-ui';
let loadingInstance;
// 
import { Message } from 'element-ui';
let messageInstance;

//axios
let instanceAxios = axios.create({
    headers:{
        'Content-Type':'application/json;charset=UTF-8',
        // token:storejs("token")
    },
    timeout:'2000',    
    //  cookie
    withCredentials:true,
    // baseURL:url    
});
// 
instanceAxios.interceptors.request.use(
  (config)=>{
    //     
    // loading
    loadingInstance = Loading.service({
        text:'...',
        background:'rgba(0,0,0,.3)'
    });        
    return config
  },
  //
  (error)=>{    
    //loading 
    loadingInstance.close();
    //      
    messageInstance = Message({
        message:'',
        type:'warning',
    });
    return Promise.reject(error)
  },
)
// 
instanceAxios.interceptors.response.use(
  (response)=>{
    //  
    //loading
    loadingInstance.close();
    const res = response.data;
    if(res.code==200){
        return res;
    }
    else if(res.code==302){
        messageInstance = Message({
            message:'',
            type:'warning',
        });
        return Promise.reject(response);
    }    
    else if(res.code==403){
        messageInstance = Message({
            message:res.message,
            type:'warning',
        });
        return Promise.reject(response);
    }    
    
  },
  (error)=>{    
    console.log(error);
    //loading
    loadingInstance.close();
    // 
    messageInstance = Message({
        message:"",
        // message:error,
        type:'warning',
    });
    return Promise.reject(error)
  },
)

//POST DELETE PUT GET
const http = {
    post(url,params){
        return instanceAxios.post(url,params).then((res)=>{
            return res;
        })
    },
    delete(url,params){
        return instanceAxios.delete(url,{params:params}).then((res)=>{
            return res;
        })
    },
    put(url,params){
        return instanceAxios.put(url,params).then((res)=>{
            return res;
        })
    },
    get(url,params){
        return instanceAxios.get(url,{params:params}).then((res)=>{
            return res;
        })
    },
};
window.instanceAxios = instanceAxios;
export default http



